html {
  font-size: 1rem;
}

body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #282828;
  font-size: 100%;
  background-color: #E4C417;
}

.menu--footer__text {
  font-size: 1em;
  font-weight: 500;
}

.statistic_block {
  height: auto;
  padding: 144px 0 !important;
  background: #1C253D;
}

.statistic {
  display: flex;
  flex-flow: row wrap;
}

.statistic__card {
  text-align: center;
  width: 25%;
  padding: 0;
  margin: 0;
}

.statistic__title {
  color: #E4C417;
  font-size: 4em;
  margin: 0;
  padding: 0;
}

.statistic__text {
  color: #fff;
  font-size: 1.25em;
  font-weight: bold;
}

.text-center {
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}

.app__content {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100vh;
  opacity: 1;
  z-index: 2;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.feature-row {
  width: 100%;
  margin: 0 auto;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
}

.feature-row .half {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 50%;
  height: 85vh;
  background-color: #1C253D;
}

.feture-row .half.mini {
  min-height: 20vh !important;
}

.feature-row .half.icon-block {
  margin: 0 !important;
  padding: 0 !important;
  background-color: #fff;
}

.feature {
  display: flex;
  flex-direction: column;
  width: 28%;
  height: 100%;
  margin: .75% auto;
  padding: 0 2% 25px;
  background-color: #fff;
}

.feature-desc {
  margin-bottom: 1vh;
  padding-bottom: 1vh;
}

.feature-desc p {
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}

.flex-row {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin: 25px 0 25px 0;
}

.flex-pic {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 50%;
  height: 20vh;
  margin: 0;
  object-fit: contain;
}

.flex-right {
  font-weight: bold;
  width: 35%;
  min-width: 120px;
  text-align: right;
}

.feature-title {
  padding: 15px;
  color: #fff;
  text-align: center;
  background-color: #1C253D;
}

.flex-right .bg-yellow {
  margin-right: -1.75em;
  padding: 10px 34px 10px 0;
}

.feature-row .half.icon-block img {
  width: 35%;
}

.feature-row .half.icon-block img.skill-icon {
  width: 100%;
}

.skills-icons .skill-icon {
  min-width: 100%;
}

.vac-row {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: center;
  max-height: 100px;
  overflow: hidden;
}

.dark {
  color: #1C253D !important;
}

.vac-icon {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  height: 100px;
}

.vac-icon img {
  width: auto;
  height: 100px;
  object-fit: cover;
}

.vac-info {
  width: 40%;
}

.feature__name {
  margin: 8px 20%;
  color: #E4C417;
  font-size: 2em;
  font-weight: 700;
  line-height: 1.5;
  text-align: center !important;
}

.feature__name.dark {
  margin: 0 20% 32px;
  font-size: 1.5em;
}

.feature__text {
  margin: 8px 20% 16px;
  color: #fff;
  font-size: 1em;
  font-weight: 600;
  line-height: 2;
  text-align: center !important;
}

.bg-light {
  background-color: #f6f7f8;
}


.bg-dark {
  background-color: #1C253D;
}

.bg-yellow {
  background-color: #E4C417;
}

.section-block {
  padding: 80px;
}


.features {
  padding: 144px 64px;
  background-size: cover;
  background-attachment: fixed;
  background-blend-mode: overlay;
}

.services__content {
  display: flex;
  justify-content: space-around;
}

.service-block {
  text-align: center;
  width: 31%;
}

.service__icon {
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 64px;
  padding: 16px;
  border-radius: 50%;
  background-color: #ffffff;
}

.service__name {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  font-size: 1.25em;
  font-weight: 700;
}

.service__text {
  min-height: 180px;
  margin: 0 5%;
  color: #fff;
  font-size: 1em;
  font-weight: 500;
}

.button-get,
.button-dark,
button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 120px;
  height: 48px;
  max-width: 60%;
  margin: 16px auto;
  padding: 0 32px;
  color: #fff;
  font-size: 1em;
  font-weight: 500;
  text-decoration: none;
  border: none;
  outline: none;
  border-radius: 30px;
  background-color: #1C253D;
}

.button-get.yellow {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 300px;
  height: 60px !important;
  padding: 0 !important;
  color: #1C253D !important;
  background-color: #E4C417;
  margin: 48px 16px 0;
}



.button-get.yellow::hover {
  color: #fff !important !important;
  background-color: #1C253D !important;
}

.button-get::hover {
  cursor: pointer !important;
}

button.noactive {
  border: none;
  color: #282828;
  cursor: default;
}



.button-white {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 120px;
  height: 48px;
  max-width: 60%;
  margin: 16px auto;
  padding: 0 32px;
  color: #1C253D;
  font-size: 1em;
  font-weight: 500;
  text-decoration: none;
  border-radius: 30px;
  background-color: #fff;
}

.button-outline {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 120px;
  height: 48px;
  max-width: 60%;
  margin: 16px auto;
  padding: 0 32px;
  color: #fff;
  font-size: 1em;
  font-weight: 500;
  text-decoration: none;
  border-radius: 30px;
  border: 2px solid #fff;
}

.yellow {
  color: #E4C417 !important;
}

.container {
  width: 90%;
  height: auto;
  margin: 0 auto;
  padding: 0;
}

.logo {
  display: flex;
  margin: 0;
  padding: 0;
}

.site-title {
  margin: -10px 0 -10px 10px;
  padding: 0;
  font-size: 1.65em;
  color: #fff;
}

.site-desc {
  margin: 0 0 0 10px;
  padding: 0;
  font-size: 1em;
  font-weight: 600;
  color: #E4C417;
}

.header {
