  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 140px;
}

.header__link {
  margin: 0 0 0 40px;
  font-size: 1em;
  font-weight: 600;
  color: #fff;
  text-decoration: none;
}

.page__header,
.header__content {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.page__navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100px;
}

.section__title {
  text-align: center;
  margin: 0 0 0 10px;
  padding: 0;
  font-size: 54px;
  font-weight: 700;
  color: #1C253D;
}

.white {
  color: #fff;
}

.subtitle__text {
  font-size: 1.15em;
  font-weight: 600;
  line-height: 1.5;
  text-align: center;
  width: 80%;
  margin: 24px auto 64px;
}


.section__title.serv,
.subtitle__text.serv {
  color: #fff;
}

.subtitle__text.serv {
  width: 70%;
  margin-bottom: 64px;
}

.about {
  width: 60%;
  margin-left: auto;
  margin-right: auto;
}




.pv-80 {
  padding-top: 80px !important;
  padding-bottom: 80px !important;
}

.pv-40 {
  padding-top: 40px !important;
  padding-bottom: 40px !important;
}

.pv-20 {
  padding-top: 20px !important;
  padding-bottom: 20px !important;
}

.ph-10 {
  margin-left: 10%;
  margin-right: 10%;
}

.ph-20 {
  margin-left: 20%;
  margin-right: 20%;
}

.ph-25 {
  margin-left: 25%;
  margin-right: 25%;
}

.text-left {
  text-align: left;
  margin-left: 0;
}

.half {
  display: inline-block;
  width: 40%;
  clear: none;
}

.flex-center {
  display: flex;
  justify-content: center;
}

@media all and (min-width: 1441px) {
  .container {
    max-width: 1440px;
  }

